<template>
  <div id="uploadImage">
    <div class="main">
      <div class="tt">联系信息</div> 
      <el-form :model="form" :rules="rules" style="margin-top:60px;width:60%" ref="form">
       <el-form-item  label="手机号" :label-width="formLabelWidth" prop="TeacherPhoneNo">
              <el-input v-model="form.TeacherPhoneNo" clearable></el-input>
            </el-form-item>
         <el-form-item label="邮箱：" :label-width="formLabelWidth" prop="TeacherEmailNo">
              <el-input v-model="form.TeacherEmailNo" clearable></el-input>
            </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button :loading="createLoading1" type="primary" @click="comfirmFormHandler('form')">
          <i class="el-icon-check el-icon--left"></i>保 存
        </el-button>
        <el-button @click="resetForm">
          <i class="el-icon-close el-icon--left"></i>重 置
        </el-button>
      </div> 
    </div>
  </div>
</template>

<script>

import http from "../../../../utils/http";
import api from "../../../../utils/api";
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return { 
      createLoading1:false,  
      userid:"",
      username:"",
      formLabelWidth: "160px", 
      form: {
        TeacherPhoneNo: "",
        TeacherEmailNo: ""
      },  
      rules: {
        TeacherPhoneNo: [
          {
            type: "phone",
            validator: api.ValidateMobile,
            // min: 2,
            // max: 256,
            message: "请输入正确的手机格式",

            trigger: "blur"
          }
        ],
        TeacherEmailNo: [
          {
            type: "email",
            // required: true,
            message: "请输入正确的邮箱地址",
            trigger: "blur"
          }
        ],
      }
    };
  },
  computed: {},
  watch: {},
   mounted() {
  let userInfo = http.getUser();
    if (userInfo) {
        this.userid = userInfo.UserID;
        this.username = userInfo.UserName;
      } 
    this.getdata();
 },
  methods: {
    async getdata() {
      const res = await http.get(api.getuserData, {
          id: this.userid,
          Token: http.getToken()
        });
        if (!res.Result) {
          this.$message({
            showClose: true,
            message: res.message,
            type: res.type
          });
        } else {
          if(res.Data != null) {  
             this.form.TeacherPhoneNo = res.Data.UserPhone;
             this.form.TeacherEmailNo = res.Data.UserMail; 
          }
        }
    },
    // 表单确认,添加用户
    comfirmFormHandler(formName) {
      this.$refs[formName].validate(valid => { 
        if (valid) { 
          this.savesubmit();
        } else {
          this.$message({
            showClose: true,
            message: "请输入正确格式",
            type: "error"
          });
          return false;
        }
      });
    },
    savesubmit: async function() {
      this.createLoading1 = true;  
      this.saveData({ 
        id: this.userid,
        phone:this.form.TeacherPhoneNo,
        email:this.form.TeacherEmailNo, 
        Token: http.getToken()
      });
    },
    async saveData(params) {
       const res = await http.post(api.setPhoneAndEmail, params);
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.$message({
          showClose: true,
          message: "数据保存成功！",
          type: "success"
        }); 
      } 
      this.createLoading1 = false;
    },
    resetForm() {
      this.$refs["form"].resetFields();
      this.$refs.form.clearValidate();
    },
  }
};
</script>

<style lang='less'>
#uploadImage {
  text-align: left;
  width: 956px;
  height: 1017px;
  background: rgba(255, 255, 255, 1);
  border-radius: 3px;
  //
  padding: 20px;
  padding-top: 0;
  .tt {
      color: #333;
      font-size: 14px;
      border-bottom: 1px solid #ecf1f7;
      line-height: 50px;
    } 
  .main {  
      .dialog-footer {
        width: 100%;
        display: block;
        text-align: center;
        margin-top: 36px;

    }
  }
}
</style>


